body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: lightpink;
    border: 1px solid #999;
    float: left;
    font-size: 200px;
    font-weight: bold;
    line-height: 34px;
    height: 240px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 240px;
  }

  .bold{
    font-weight: bold;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  
  .reset-button {
    margin: 25px;
    font-size: 25px;
    color: yellow;
    background: purple; 
  }